import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";

@Component({
  selector: "app-schedule-of-rates",
  templateUrl: "./schedule-of-rates.component.html",
  styleUrls: ["./schedule-of-rates.component.scss"]
})
export class ScheduleOfRatesComponent implements OnInit {
  public name: string;
  public url: string;
  public add: boolean
  constructor(private router: Router) {}
  ngOnInit() {
    this.display()
    this.router.events.subscribe(event => {
      this.display()
    });
  }
  display(){
    this.url = window.location.href;
    if (this.url.indexOf("add-rate") != -1) {
      this.name = "设置";
      this.add = false
    } else {
      this.name = "";
      this.add = true
    }
  }

}
