import { Component, OnInit } from "@angular/core";
import { HttpParams } from "@angular/common/http";
import { ActivatedRoute,Router } from "@angular/router";
import { HttpService } from "../http.service";
@Component({
  selector: "app-add-rate",
  templateUrl: "./add-rate.component.html",
  styleUrls: ["./add-rate.component.scss"]
})
export class AddRateComponent implements OnInit {
  ruleLists: any;  //新增费率参数
  formArr = [      //费率规则数组
    {
      timeCodeStart: "0",
      timeCodeEnd: "15",
      timingUnit: "15",
      price: "0"
    }
  ];
  rateid;         //费率id
  lists;          //费率详情内容
  disable = false;//基本费率区域显示隐藏
  show = false;   //执行计划区域显示隐藏
  addenable;      //添加正在启用区域显示隐藏
  Waiting;        //添加待执行区域显示隐藏
  ings = false;   //显示正在启用执行区域
  ing;            //提示信息显示隐藏
  wait;           //待执行区域显示隐藏
  new = false;    //控制新增按钮
  changerule;     //修改费率按钮
  changeTime;     //执行费率最后修改时间
  scheduleId = 0; //执行计划id
  ratename;       //费率名称
  regionId = 1;   //区域id
  operator = 10;  //管理员id
  dateType = [];  //待启用日期类型   1
  supplier = [];  //待启用泊位供应商  2
  area = [];      //待启用区域  3
  street = [];    //待启用街道  4
  customCalendar = []; //待启用自定义日历   5
  dateTypeE = []; //日期类型   1
  supplierE = []; //泊位供应商  2
  areaE = [];     //区域  3
  streetE = [];   //街道  4
  customCalendarE = []; //自定义日历   5
  noenable;       //待启用执行计划数据
  dateVal;        //执行计划生效时间
  billableTime = "08:00-21:00"; //正在启用收费时段
  waitTime;       //待启用收费时段
  roadLevel;      //道路级别
  roadLevels = []; //用户选择的道路级别
  regionalLevel;  //区域级别渲染数据
  regionalList = []; //用户选择的区域级别数据
  calendarList;   //日期标签渲染数据
  calendarLists = []; //用户选择的日期标签
  supplierList;   //供应商渲染数据
  suppliers = []; //用户选择的泊位提供商
  customList;     //渲染的自定义日历标签
  customLists = []; //用户选择的日历标签
  road: any = {     //执行计划参数
    effective: "",
    startChargingTime: "",
    endChargingTime: "",
    operator: Number(),
    labels: <any>[]
  }; 
  labels = [];
  constructor(
    private https: HttpService,
    private router:Router,
    private activeRoute: ActivatedRoute
  ) {}
  ngOnInit() {
    this.getRoadData();
    this.activeRoute.params.subscribe(params => {
      this.rateid = params["rateid"];
      if (this.rateid != "new") {
        this.getRateInfo();
      }
    });
  }
  // 新增费率
  addRate() {
    console.log(this.formArr);
    let list = JSON.stringify(this.formArr);
    this.ruleLists = {
      regionId: this.regionId,
      rateName: this.ratename,
      operator: this.operator,
      ruleList: list
    };
    const params = new HttpParams({
      fromObject: this.ruleLists
    });
    this.https.addRate(params).subscribe(res => {
      let Res;
      Res = res;
      if (Res.head.status == "succ") {
        alert("添加成功");
        this.router.navigateByUrl("/pages/rate-page/schedule-of-rates/add-rate/"+Res.body.rateId)
      }
    });
  }
  // 删除某一条费率规则
  delRateRule(index){
    this.formArr.splice(index,1)
  }
  // 修改费率
  editorRate(){
    let list = JSON.stringify(this.formArr);
    this.ruleLists = {
      regionId: this.regionId,
      rateName: this.ratename,
      operator: this.operator,
      ruleList: list
    };
    const params = new HttpParams({
      fromObject: this.ruleLists
    });
    this.https.editorRate(this.rateid,params).subscribe(res=>{
      console.log(res);
      
      this.getRateInfo()
    })
  }
  // 执行计划参数
  scheduleData() {
    for (let i = 0; i < this.regionalList.length; i++) {
      this.labels.push({
        type:2,
        category: 2,
        labelId: parseInt(this.regionalList[i]),
        labelContent: this.regionalList[i].replace(/[0-9]/g, "")
      });
    }
    for (let i = 0; i < this.suppliers.length; i++) {
      this.labels.push({
        type:2,
        category: 1,
        labelId: parseInt(this.suppliers[i]),
        labelContent: this.suppliers[i].replace(/[0-9]/g, "")
      });
    }
    for (let i = 0; i < this.roadLevels.length; i++) {
      this.labels.push({
        type:2,
        category: 3,
        labelId: parseInt(this.roadLevels[i]),
        labelContent: this.roadLevels[i].replace(/[0-9]/g, "")
      });
    }
    for (let i = 0; i < this.calendarLists.length; i++) {
      this.labels.push({
        type:1,
        category: 1,
        labelId: parseInt(this.calendarLists[i]),
        labelContent: this.calendarLists[i].replace(/[0-9]/g, "")
      });
    }
    for (let i = 0; i < this.customLists.length; i++) {
      this.labels.push({
        type:1,
        category: 2,
        labelId: parseInt(this.customLists[i]),
        labelContent: this.customLists[i].replace(/[0-9]/g, "")
      });
    }
    let label = JSON.stringify(this.labels);
    if (!this.dateVal) {
      this.road = {
        startChargingTime: this.billableTime.split("-")[0],
        endChargingTime: this.billableTime.split("-")[1],
        operator: 10,
        labels: label
      };
    }else{
      this.dateVal = this.dateVal.toLocaleString();
      this.dateVal = this.dateVal.split(" ")[0];
      this.dateVal = this.dateVal.replace(/\//g,"-")
      this.dateVal = this.dateVal.split("-");
      for (let i = 0; i < this.dateVal.length; i++) {
        if (this.dateVal[i]<10) {
          this.dateVal[i] = "0"+this.dateVal[i]
        }
      }
      this.dateVal=this.dateVal.toString();
      this.dateVal = this.dateVal.replace(/,/g,"-");
      this.road = {
        effective: this.dateVal,
        startChargingTime: this.billableTime.split("-")[0]+":00",
        endChargingTime: this.billableTime.split("-")[1]+":00",
        operator: 10,
        labels: label
      };
    }
    console.log(this.road);
    return this.road;
  }
  // 新增执行计划
  addSchedule() {
    const params = new HttpParams({
      fromObject: this.scheduleData()
    });
    console.log(params);
    this.https.addSchedule(this.rateid, params).subscribe(res => {
      console.log(res);
      let Res;
      Res = res;
      if (Res.head.status == "succ") {
        this.wait = true;
        this.Waiting = false;
        this.getRateInfo();
      }
      if (Res.head.errors) {
        alert(Res.head.errors[0].message)
      }
    }); 
    this.labels = [];
  }
  // 修改未启用执行计划
  editorSchedule() {
      const params = new HttpParams({
        fromObject: this.scheduleData()
      });
      this.https.editorSchedule(this.rateid , this.scheduleId, params).subscribe(res => {
        console.log(res);
        let Res;
        Res = res;
        if (Res.head.status == "succ") {
          this.wait = true;
          this.Waiting = false;
          this.getRateInfo();
        }
        if (res.head.errors) {
          alert(res.head.errors[0].message)
        }
      });
  }
  // 删除未启用执行计划
  deleteSchedule() {
    this.https.deleteSchedule(this.scheduleId).subscribe(res => {
      console.log(res);
      let Res;
      Res = res;
      if (Res.head.status == "succ") {
        this.wait = false;
        this.getRateInfo();
      }
    });
  }
  // 获取泊位与日历信息
  getRoadData() {
    // 获取道路级别
    let params = new HttpParams({
      fromObject: { tagPrefix: "道路级别" }
    });
    this.https.getRoadLevel(params).subscribe(res => {
      this.roadLevel = res;
    });
    // 获取区域级别
    let areaparams = new HttpParams({
      fromObject: { tagPrefix: "区域类别" }
    });
    this.https.getAreaLevel(areaparams).subscribe(res => {
      this.regionalLevel = res;
    });
    // 获取泊位提供商
    this.https.getSupplier().subscribe(res => {
      this.supplierList = res;
    });
    // 获取日期类型
    this.https.getCalendarType().subscribe(res => {
      this.calendarList = res;
      console.log(this.calendarList);
      
    });
    // 获取自定义日期标签
    this.https.getCustomCalendar().subscribe(res => {
      this.customList = res;
      console.log(this.customList);
      
    });
  }
  // 获取费率详情
  getRateInfo() {
    this.disable = true;
    this.show = true;
    this.new = true;
    this.customCalendar = [];
    this.street = [];
    this.area = [];
    this.supplier = [];
    this.dateType = [];
    this.customCalendarE = [];
    this.streetE = [];
    this.areaE = [];
    this.supplierE = [];
    this.dateTypeE = [];
    this.regionalList = [];
    this.roadLevels = [];
    this.suppliers = [];
    this.calendarLists = [];
    this.customLists = [];
    this.labels = [];
    this.https.getRateInfo(this.rateid).subscribe(res => {
      console.log(res);
      this.lists = res;
      this.formArr = this.lists.body[0].ruleList
      this.ratename = this.lists.body[0].rateName;
      if (this.lists.body[0].schedule.length != 0) {
        this.changerule = false;
        this.scheduleId = this.lists.body[0].schedule[0].scheduleId;
        for (let i = 0; i < this.lists.body[0].schedule.length; i++) {
          if (this.lists.body[0].schedule[i].status == 1) {
            if (this.lists.body[0].schedule[i].modifyOn) {
              this.changeTime = this.lists.body[0].schedule[i].modifyOn.date.split(".")[0];
            }else{
              this.changeTime = "未设置"
            }
            this.ings = true;
            this.ing = true;
            for (let j = 0;j < this.lists.body[0].schedule[i].labels.length;j++
            ) {
              if (this.lists.body[0].schedule[i].labels[j].type == 1) {
                switch (this.lists.body[0].schedule[i].labels[j].category) {
                  case 1:
                    this.dateTypeE.push(
                      this.lists.body[0].schedule[i].labels[j].labelContent
                    );
                    break;
                  case 2:
                    this.customCalendarE.push(
                      this.lists.body[0].schedule[i].labels[j].labelContent
                    );
                    break;
                }
              }else{
                switch (this.lists.body[0].schedule[i].labels[j].category) {
                  case 1:
                    this.supplierE.push(
                      this.lists.body[0].schedule[i].labels[j].labelContent
                    );
                    break;
                  case 2:
                    this.areaE.push(
                      this.lists.body[0].schedule[i].labels[j].labelContent
                    );
                    break;
                  case 3:
                    this.streetE.push(
                    this.lists.body[0].schedule[i].labels[j].labelContent
                    );
                  break;
                }
              }
            }
          }
          if (this.lists.body[0].schedule[i].status == 2) {
            this.wait = true;
            this.noenable = this.lists.body[0].schedule[i];
            this.noenable.startChargingTime = this.noenable.startChargingTime.slice(
              0,
              5
            );
            this.noenable.endChargingTime = this.noenable.endChargingTime.slice(
              0,
              5
            );
            this.waitTime =
              this.noenable.startChargingTime +"-" +this.noenable.endChargingTime;
              for (let j = 0;j < this.lists.body[0].schedule[i].labels.length;j++
              ) {
  
                if (this.lists.body[0].schedule[i].labels[j].type == 1) {
                  switch (this.lists.body[0].schedule[i].labels[j].category) {
                    case 1:
                      this.dateType.push(
                        this.lists.body[0].schedule[i].labels[j].labelContent
                      );
                      break;
                    case 2:
                      this.customCalendar.push(
                        this.lists.body[0].schedule[i].labels[j].labelContent
                      );
                      break;
                  }
                }else{
                  switch (this.lists.body[0].schedule[i].labels[j].category) {
                    case 1:
                      this.supplier.push(
                        this.lists.body[0].schedule[i].labels[j].labelContent
                      );
                      break;
                    case 2:
                      this.area.push(
                        this.lists.body[0].schedule[i].labels[j].labelContent
                      );
                      break;
                    case 3:
                      this.street.push(
                      this.lists.body[0].schedule[i].labels[j].labelContent
                      );
                    break;
                  }
                }
              }
          }
        }
      }else{
        this.changerule = true;
      }
    });
  }
  // 添加费率规则
  addRuleList() {
    let des = {
      timeCodeStart: undefined,
      timeCodeEnd: undefined,
      timingUnit: undefined,
      price: undefined
    };
    this.formArr.push(des);
  }
  // 改变添加按钮
  changeAddBut(){
    this.changerule = false;
  }
}