import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { RateListComponent } from "./rate-list/rate-list.component";
import { ScheduleOfRatesComponent } from "./schedule-of-rates.component";
import { AddRateComponent } from "../add-rate/add-rate.component";
const routes: Routes = [
  {
    path: "",
    component: ScheduleOfRatesComponent,
    children: [
      {
        path: "rate-list",
        component: RateListComponent
      },
      {
        path:"add-rate/:rateid",
        component:AddRateComponent
      },
      {
        path: "",
        redirectTo: "rate-list",
        component: RateListComponent
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ScheduleOfRatesRoutingModule {}
